@import "@/assets/global.scss";


















.main {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: flex-start;

  > img {
    margin-bottom: 8px;
    width: calc((100% - 16px) / 2);
    object-fit: cover;
  }

  > img:nth-child(odd) {
    margin-right: 16px;
  }
}
